import React from 'react';
import './App.css'; // 

function HomePage() {
  return (
    <div>
      <h1>
        <a href="https://www.google.com/search?q=GitHub+WTS25kt" target="_blank" rel="noreferrer">Web</a>
        <div className="tooltip" onClick={toggleTooltip}>
          <div className="tooltiptext">
            <i className="fas fa-check-circle"></i> 0. :AI<br />
            <a href="https://github.com/WTS25kt" target="_blank" rel="noreferrer"><i className="fas fa-mobile-alt"></i> 1. GitHub</a><br />
            <a href="https://github.com/WTS25kt" target="_blank" rel="noreferrer"><i className="fas fa-mobile-alt"></i> 2. iPhoneGitHub</a><br />
            <i className="fas fa-mobile-alt"></i> 3. AI<br />
            <i className="fas fa-mobile-alt"></i> 4. 2<br />
            <i className="fas fa-mobile-alt"></i> 5. 24<br />
            <i className="fas fa-laptop"></i> 6. MacBookGitHub Desktop
          </div>
        </div>
      </h1>
      <h2></h2>
      <p><a href="/readme-generator">README</a></p>
    </div>
  );
}

function toggleTooltip() {
  const tooltip = document.querySelector('.tooltip');
  if (tooltip.classList.contains('tooltip-active')) {
    tooltip.classList.remove('tooltip-active');
  } else {
    tooltip.classList.add('tooltip-active');
  }
}

export default HomePage;